
import { defineComponent, onMounted, ref } from 'vue';

export default defineComponent({
	props: {
		message: String,
	},
	// eslint-disable-next-line
	setup(props) {
		let showing = ref(false);

		onMounted(() => (showing.value = true));

		function close() {
			showing.value = false;
		}

		return { showing, close };
	},
});
